.createlist {
  height: 50px;
  font-size: 1rem;
  font-weight: 700;
  color: #121212;
  border: 2px solid #444444;
  border-radius: 7px;
  background: none;
  transition: all .2s;
}

.createlist:hover {
  font-size: 1.1rem;
  border: 2px solid #FBBC04;
  color: #5e5e5e;
  cursor: pointer;
}